  // BNBTJCD
  const buyWithBNB = async () => {
    if (!tokenContract || !tradeAmount || !exchangeRate || parseFloat(exchangeRate) <= 0) {
      alert("");
      return;
    }
    
    setLoading(true);
    try {
      // BNB
      const tjcdAmount = parseFloat(tradeAmount);
      const bnbAmount = tjcdAmount * parseFloat(exchangeRate);
      const bnbWei = ethers.utils.parseEther(bnbAmount.toString());
      
      console.log("TJCD:", tjcdAmount);
      console.log("BNB:", bnbAmount);
      
      // Gas
      const gasPrice = await provider.getGasPrice();
      // 20%Gas
      const adjustedGasPrice = gasPrice.mul(120).div(100);
      // Gas
      const gasLimit = 100000;
      // Gas
      const estimatedGasCost = adjustedGasPrice.mul(gasLimit);
      
      // BNBGas
      const bnbBalance = await provider.getBalance(account);
      const totalCost = bnbWei.add(estimatedGasCost);
      
      console.log("BNB:", ethers.utils.formatEther(bnbBalance));
      console.log(":", ethers.utils.formatEther(bnbWei));
      console.log("Gas:", ethers.utils.formatEther(estimatedGasCost));
      console.log(":", ethers.utils.formatEther(totalCost));
      
      if (bnbBalance.lt(totalCost)) {
        throw new Error("BNBGasBNB");
      }
      
      // BNB
      const signer = await provider.getSigner();
      const tx = await signer.sendTransaction({
        to: receiverAddress,
        value: bnbWei,
        gasLimit: gasLimit,
        gasPrice: adjustedGasPrice
      });
      
      console.log("BNB:", tx.hash);
      const receipt = await tx.wait();
      console.log("BNB:", receipt);
      
      // TJCD
      alert("BNBTJCD");
      
      // 
      if (account) {
        updateBalanceAndAllowance(account, tokenContract, escrowContract);
      }
    } catch (error) {
      console.error("BNB:", error);
      
      // 
      let errorMessage = "BNB";
      
      // Gas
      if (error.message && (error.message.includes("gas") || error.message.includes("Gas") || 
          error.message.includes("insufficient funds") || error.message.includes(""))) {
        errorMessage = "Gas  BNB  Gas ";
      } else if (error.reason) {
        errorMessage += ": " + error.reason;
      } else if (error.message) {
        errorMessage += ": " + error.message;
      }
      
      alert(errorMessage);
    } finally {
      setLoading(false);
    }
  };
  
